<template>
    <v-container class="grey lighten-4 ma-5">
        <h1 class="text-xs-center">البحث عن المراكز</h1>
        <v-layout row wrap>
            <v-flex xs12 md3>
                <v-select
                offset-y
                class="ma-4"
                v-model="serviceType"
                :items="serviceTypes"
                label="نوع الخدمه"
                hide-details
                single-line
                @change="() => selectedItem(serviceType)"
                ></v-select>
            </v-flex>
            <v-flex xs12 md3>
                <v-select
                offset-y
                class="ma-4"
                v-model="service"
                :items="services"
                label="الخدمه"
                hide-details
                single-line
                ></v-select>
            </v-flex>
            <v-flex xs12 md2>
                <v-select
                offset-y
                class="ma-4"
                v-model="discount"
                :items="discounts"
                label="الخصم"
                hide-details
                single-line
                ></v-select>
            </v-flex>
            <v-flex xs12 md2>
                <v-select
                offset-y
                class="ma-4"
                v-model="price"
                :items="prices"
                label="السعر"
                hide-details
                single-line
                ></v-select>
            </v-flex>
            <v-flex xs12 md2>
                <v-select
                offset-y
                class="ma-4"
                v-model="evalution"
                :items="evalutions"
                label="التقيم"
                hide-details
                single-line
                ></v-select>
            </v-flex>
        </v-layout>
        <v-container fluid>
            <v-layout row wrap>
                <v-flex xs12 md3 v-for="center in filteredCenters" :key="center.id">
                    <v-card flat class="ma-2">
                        <v-card-text class="ma-4 pa-4">
                            <v-layout row column>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            اسم المركز
                                        </v-flex>
                                        <v-flex class=" " flat router :to="`/center/profile/${center.id}`">
                                            {{ center.name }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            تقيم الخدمات
                                        </v-flex>
                                        <v-flex>
                                            {{ center.service_rate }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            تقيم نوع الخدمه
                                        </v-flex>
                                        <v-flex flat>
                                            {{ center.service_type_rate }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            تقيم الخصومات
                                        </v-flex>
                                        <v-flex flat>
                                            {{ center.discount_rate }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            تقيم الاسعالر
                                        </v-flex>
                                        <v-flex flat>
                                            {{ center.price_rate }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            تقيم المركز
                                        </v-flex>
                                        <v-flex flat>
                                            {{ center.evalution_rate }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            عنوان المركز
                                        </v-flex>
                                        <v-flex class=" " flat router :to="`/maps/center/${center.id}`">
                                            {{ center.address }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                                <v-flex md2 class="ma-1">
                                    <v-layout row wrap>
                                        <v-flex class="grey--text">
                                            رقم المركز
                                        </v-flex>
                                        <v-flex class=" " flat>
                                            {{ center.number }}
                                        </v-flex>
                                    </v-layout>
                                </v-flex>
                            </v-layout>
                        </v-card-text>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>
    </v-container>
</template>

<script>
/* eslint-disable */
import CenterServec from '../services/CenterServec'
export default {
    name: 'CenterSearch',
    data(){
        return {
            apiCenters: [],
            centers: [
                { 
                    id: '1',
                    name: 'مركز جده',
                    address: 'جده حي الرياض',
                    number: '123456789',
                    service: 'خلع ضرس',
                    type: 'تجميل',
                    service_rate: '3',
                    service_type_rate: '5',
                    evalution_rate: '5',
                    discount_rate: '2',
                    price_rate: '3'
                },
                { 
                    id: '2',
                    name: 'مركز المدينه',
                    address: 'المدينه المهندسين',
                    number: '123456789',
                    service: 'تركيب سن',
                    type: 'اسنان',
                    service_rate: '4',
                    service_type_rate: '1',
                    evalution_rate: '4',
                    discount_rate: '2',
                    price_rate: '3'
                },
                { 
                    id: '3',
                    name: 'مركز الشرقيه',
                    address: 'الشرقيه حي العمال',
                    number: '123456789',
                    service: 'تقويم',
                    type: 'ولاده',
                    service_rate: '3',
                    service_type_rate: '3',
                    evalution_rate: '1',
                    discount_rate: '4',
                    price_rate: '2'
                },
                { 
                    id: '4',
                    name: 'مركز خالد ابن الوليد',
                    address: 'الشرقيه حي العمال',
                    number: '123456789',
                    service: 'خلع ضرس',
                    type: 'اسنان',
                    service_rate: '4',
                    service_type_rate: '4',
                    evalution_rate: '3',
                    discount_rate: '1',
                    price_rate: '4'
                },
                { 
                    id: '5',
                    name: 'مركز بن سلمان',
                    address: 'مكه',
                    number: '123456789',
                    service: 'تبيض اسنان',
                    type: 'تجميل',
                    service_rate: '3',
                    service_type_rate: '5',
                    evalution_rate: '1',
                    discount_rate: '1',
                    price_rate: '3'
                }
            ],
            service: '',
            services: [ 'الكل', 'خلع ضرس', 'تقويم', 'تبيض اسنان' ],
            serviceType: '',
            serviceTypes: [ 'الكل', 'اسنان', 'تجميل', 'ولاده' ],
            discount: '',
            discounts: [ 'الكل', '5%', '10%' , '15%' , '20%' ],
            price: 0,
            prices: [ 'الكل', 500, 1000, 1500, 2000, 2500 ],
            evalution: '',
            evalutions: [ 'الكل', '1', '2', '3', '4', '5' ],
        }
    },
    computed: {
        serviceRate(){
            switch (this.service) {
                case 'اسنان':
                    return '1'
                    break;
                case 'تقويم':
                    return '5'
                    break;
                case 'تبيض اسنان':
                    return '4'
                    break;
                case 'خلع ضرس':
                    return '3'
                    break;
                default:
                    return '0'
                    break;
            }
        },
        serviceTypeRate(){
            switch (this.serviceType) {
                case 'ولاده':
                    return '1'
                    break;
                case 'تجميل':
                    return '5'
                    break;
                case 'اسنان':
                    return '4'
                    break;
                default:
                    return '0'
                    break;
            }
        },
        discountRate(){
            switch (this.discount) {
                case '5%':
                    return '1'
                    break;
                case '10%':
                    return '2'
                    break;
                case '15%':
                    return '3'
                    break;
                case '20%':
                    return '4'
                    break;
                default:
                    return '0'
                    break;
            }
        },
        priceRate(){
            if (this.price <= 500) {
                return '1'
            } else {
                if (this.price <= 1000) {
                    return '2'
                } else {
                    if (this.price <= 1500) {
                        return '3'
                    } else {
                        if (this.price <= 2000) {
                            return '4'
                        } else {
                            if (this.price <= 2500) {
                                return '5'
                            } else {
                                return '0'
                            }
                        }
                    }
                }
            }
        },
        filteredCenters(){
            return this.centers.filter(center => {
               return center.price_rate == this.priceRate 
               || center.evalution_rate == this.evalution
               || center.service_type_rate == this.serviceTypeRate
               || center.discount_rate == this.discountRate
            })
        }
    },
    methods: {
        selectedItem(){
            console.log(this.apiCenters)
        }
    },
    created() {
        CenterServec.index().then( res => {
            this.apiCenters = res.data.response
        })
    },
}
</script>
